* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.chat-app {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  height: 100vh;
  background-color: #ffffff;
  color: #000000;
  transition: all 0.3s ease;
}

.chat-app.dark {
  background-color: #111827;
  color: #ffffff;
}

.chat-container {
  display: flex;
  height: 100vh;
}

/* Sidebar Styles */
.sidebar {
  width: 320px;
  background-color: #f3f4f6;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  transition: background-color 0.3s ease;
}

.dark .sidebar {
  background-color: #1f2937;
}

.sidebar-title {
  font-weight: 600;
  font-size: 0.875rem;
  margin-bottom: 1rem;
}

.dropdown-section {
  position: relative;
}

.dropdown-section label {
  display: block;
  font-size: 0.75rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #6b7280;
}

.dark .dropdown-section label {
  color: #d1d5db;
}

.dropdown-button {
  width: 100%;
  background-color: #e5e7eb;
  border: none;
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  font-size: 0.75rem;
  font-weight: 500;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.dropdown-button:hover {
  background-color: #d1d5db;
}

.dark .dropdown-button {
  background-color: #374151;
  color: white;
}

.dark .dropdown-button:hover {
  background-color: #4b5563;
}

.dropdown-arrow {
  width: 1rem;
  height: 1rem;
  fill: none;
  stroke: currentColor;
  stroke-width: 2;
  transition: transform 0.2s ease;
}

.dropdown-arrow.open {
  transform: rotate(180deg);
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: #e5e7eb;
  border-radius: 0.5rem;
  margin-top: 0.25rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1);
  z-index: 10;
  max-height: 12rem;
  overflow-y: auto;
}

.dark .dropdown-menu {
  background-color: #374151;
}

.dropdown-menu button {
  width: 100%;
  padding: 0.5rem 1rem;
  font-size: 0.75rem;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  transition: background-color 0.2s ease;
  color: inherit;
}

.dropdown-menu button:hover {
  background-color: #d1d5db;
}

.dark .dropdown-menu button:hover {
  background-color: #4b5563;
}

.dropdown-divider {
  height: 1px;
  background-color: #9ca3af;
  margin: 0.25rem 0;
}

.dark .dropdown-divider {
  background-color: #4b5563;
}

.action-button {
  background-color: #e5e7eb;
  border: none;
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.action-button:hover {
  background-color: #d1d5db;
}

.dark .action-button {
  background-color: #374151;
  color: white;
}

.dark .action-button:hover {
  background-color: #4b5563;
}

.reset-button {
  color: #dc2626;
  background-color: #f3f4f6;
}

.reset-button:hover {
  background-color: #e5e7eb;
}

.dark .reset-button {
  color: #f87171;
  background-color: #1f2937;
}

.dark .reset-button:hover {
  background-color: #374151;
}

.dark-mode-toggle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: auto;
  padding-top: 1.5rem;
}

.dark-mode-toggle label {
  font-size: 0.75rem;
  font-weight: 500;
}

.toggle-switch {
  position: relative;
  width: 2.75rem;
  height: 1.5rem;
  background-color: #374151;
  border-radius: 0.75rem;
  cursor: pointer;
  transition: background-color 0.3s;
  border: none;
}

.toggle-switch.active {
  background-color: #10b981;
}

.toggle-slider {
  position: absolute;
  top: 2px;
  left: 2px;
  width: 1.25rem;
  height: 1.25rem;
  background-color: white;
  border-radius: 50%;
  transition: transform 0.3s;
}

.toggle-switch.active .toggle-slider {
  transform: translateX(1.25rem);
}

/* Main Chat Area */
.chat-main {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
}

.chat-title {
  text-align: center;
  font-size: 1.25rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
}

.messages-container {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 1.5rem;
  padding-right: 0.5rem;
}

.welcome-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #6b7280;
  text-align: center;
}

.dark .welcome-message {
  color: #9ca3af;
}

.welcome-message p:first-child {
  font-size: 1.125rem;
  margin-bottom: 0.5rem;
}

.message {
  display: flex;
  margin-bottom: 1rem;
  animation: fadeInUp 0.3s ease-out;
}

.message.user {
  justify-content: flex-end;
}

.message.bot {
  justify-content: flex-start;
}

.message-content {
  background-color: #e5e7eb;
  padding: 1rem;
  border-radius: 1rem;
  max-width: 24rem;
  word-wrap: break-word;
  transition: background-color 0.3s ease;
}

.message.user .message-content {
  margin-left: 3rem;
}

.message.bot .message-content {
  margin-right: 3rem;
}

.dark .message-content {
  background-color: #374151;
}

.message.error .message-content {
  border-left: 4px solid #dc2626;
}

.image-caption {
  margin-top: 0.5rem;
  font-size: 0.75rem;
  color: #6b7280;
  font-style: italic;
}

.dark .image-caption {
  color: #9ca3af;
}

.typing-indicator {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  padding: 1rem;
}

.typing-dot {
  width: 0.5rem;
  height: 0.5rem;
  background-color: #6b7280;
  border-radius: 50%;
  animation: typingBounce 1.4s infinite;
}

.typing-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-dot:nth-child(3) {
  animation-delay: 0.4s;
}

@keyframes typingBounce {
  0%, 60%, 100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-0.625rem);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(0.625rem);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Markdown Content Styles */
.markdown-content {
  line-height: 1.6;
  word-wrap: break-word;
}

.markdown-content h1,
.markdown-content h2,
.markdown-content h3,
.markdown-content h4,
.markdown-content h5,
.markdown-content h6 {
  margin: 0.5em 0 0.3em 0;
  font-weight: 600;
}

.markdown-content p {
  margin: 0.5em 0;
}

.markdown-content ul,
.markdown-content ol {
  margin: 0.5em 0;
  padding-left: 1.5em;
}

.markdown-content li {
  margin: 0.2em 0;
}

.markdown-content blockquote {
  border-left: 4px solid #10b981;
  margin: 1em 0;
  padding: 0.5em 1em;
  background-color: rgba(16, 185, 129, 0.1);
  border-radius: 0 0.25rem 0.25rem 0;
}

.dark .markdown-content blockquote {
  background-color: rgba(16, 185, 129, 0.2);
}

/* Code Styling */
.markdown-content pre {
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  padding: 0;
  margin: 1em 0;
  overflow-x: auto;
}

.dark .markdown-content pre {
  background-color: #1e1e1e;
}

.code-block-wrapper {
  position: relative;
  margin: 1em 0;
}

.code-block-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #e5e7eb;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem 0.5rem 0 0;
  font-size: 0.8rem;
  border-bottom: 1px solid #d1d5db;
}

.dark .code-block-header {
  background-color: #2d3748;
  border-bottom-color: #4a5568;
}

.code-language {
  font-weight: 500;
  color: #6b7280;
}

.dark .code-language {
  color: #a0aec0;
}

.copy-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 0.25rem;
  transition: background-color 0.2s;
}

.copy-button:hover {
  background-color: rgba(0, 0, 0, 0.1);
}

.dark .copy-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.markdown-content code {
  padding: 1rem;
  display: block;
  overflow-x: auto;
  border-radius: 0 0 0.5rem 0.5rem;
}

.inline-code {
  background-color: #f1f5f9 !important;
  padding: 0.15em 0.3em !important;
  border-radius: 0.25rem !important;
  font-size: 0.9em !important;
  display: inline !important;
}

.dark .inline-code {
  background-color: #374151 !important;
}

/* Link Styling */
.message-link {
  color: #10b981;
  text-decoration: underline;
  transition: color 0.2s;
}

.message-link:hover {
  color: #059669;
}

.dark .message-link {
  color: #34d399;
}

.dark .message-link:hover {
  color: #6ee7b7;
}

/* Table Styling */
.table-wrapper {
  overflow-x: auto;
  margin: 1em 0;
}

.message-table {
  border-collapse: collapse;
  width: 100%;
  min-width: 300px;
}

.message-table th,
.message-table td {
  border: 1px solid #d1d5db;
  padding: 0.5rem;
  text-align: left;
}

.message-table th {
  background-color: #f9fafb;
  font-weight: 600;
}

.dark .message-table th,
.dark .message-table td {
  border-color: #4b5563;
}

.dark .message-table th {
  background-color: #374151;
}

/* Improved message spacing */
.message-content .markdown-content > *:first-child {
  margin-top: 0;
}

.message-content .markdown-content > *:last-child {
  margin-bottom: 0;
}

/* Task lists */
.markdown-content input[type="checkbox"] {
  margin-right: 0.5em;
}

/* Input Area */
.input-container {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.upload-button, .send-button {
  padding: 0.75rem;
  background-color: #e5e7eb;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.upload-button:hover, .send-button:hover {
  background-color: #d1d5db;
}

.dark .upload-button, .dark .send-button {
  background-color: #374151;
}

.dark .upload-button:hover, .dark .send-button:hover {
  background-color: #4b5563;
}

.upload-button svg, .send-button svg {
  width: 1.5rem;
  height: 1.5rem;
  fill: none;
  stroke: currentColor;
  stroke-width: 2;
}

.message-input {
  flex: 1;
  background-color: #f3f4f6;
  border: none;
  border-radius: 0.5rem;
  padding: 0.75rem 1rem;
  font-size: 1rem;
  outline: none;
  transition: all 0.3s ease;
}

.message-input:focus {
  outline: 2px solid #10b981;
}

.dark .message-input {
  background-color: #1f2937;
  color: white;
}

.message-input::placeholder {
  color: #6b7280;
}

.dark .message-input::placeholder {
  color: #9ca3af;
}

.send-button:disabled, .upload-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.spinner {
  width: 1.5rem;
  height: 1.5rem;
  border: 2px solid #f3f4f6;
  border-top: 2px solid currentColor;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Modal Styles */
.modal-backdrop {
  position: fixed;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
}

.modal {
  background-color: white;
  border-radius: 0.5rem;
  width: 24rem;
  padding: 1.5rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  border: 1px solid #d1d5db;
}

.dark .modal {
  background-color: #1f2937;
  border-color: #4b5563;
}

.modal h2 {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1rem;
  text-align: center;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  font-size: 0.75rem;
  font-weight: 500;
  margin-bottom: 0.5rem;
  color: #6b7280;
}

.dark .form-group label {
  color: #d1d5db;
}

.form-group input, .form-group textarea {
  width: 100%;
  padding: 0.75rem;
  border-radius: 0.5rem;
  border: 1px solid #d1d5db;
  background-color: #f3f4f6;
  font-size: 1rem;
  transition: all 0.3s ease;
}

.form-group input:focus, .form-group textarea:focus {
  outline: 2px solid #10b981;
  border-color: #10b981;
}

.dark .form-group input, .dark .form-group textarea {
  background-color: #374151;
  border-color: #4b5563;
  color: white;
}

.form-group textarea {
  resize: none;
}

.modal-actions {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  margin-top: 1.5rem;
}

.modal-actions button {
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
  border: none;
}

.modal-actions button[type="button"] {
  background-color: #e5e7eb;
  color: #374151;
}

.modal-actions button[type="button"]:hover {
  background-color: #d1d5db;
}

.modal-actions button[type="submit"] {
  background-color: #10b981;
  color: white;
}

.modal-actions button[type="submit"]:hover {
  background-color: #059669;
}

.dark .modal-actions button[type="button"] {
  background-color: #374151;
  color: white;
}

.dark .modal-actions button[type="button"]:hover {
  background-color: #4b5563;
}

/* Responsive Design */
@media (max-width: 768px) {
  .sidebar {
    width: 280px;
  }
  
  .message-content {
    max-width: 20rem;
  }
}
